import { DataPage, EventData, DataType, EventDataType, NetworkData, BuiltInName } from '@zyb-data/stats-core'
import StatsMain from '../core'
import { D } from '../core/debug'

const originApp = App
const originPage = Page
const originComponent = Component
const originRequest = wx.request

const ErrCPDoNotExist = '$cp do not exist.'

type WXFunc = (option: any) => void

export class StatsAppFactoryWX {
  private appInstance!: StatsAppWX
  constructor(private $sm: StatsMain) {}
  /**
   * override wx Functions
   */
  init() {
    App = this.App
    Page = this.Page
    Component = this.Component
    Object.defineProperty(wx, 'request', {
      writable: true
    })
    wx.request = this.Request
  }
  get App(): WXFunc {
    return (option: any) => {
      const appInstance = new StatsAppWX(this.$sm, option)
      appInstance.init()
      this.appInstance = appInstance
      return originApp(appInstance.option)
    }
  }
  get Page(): WXFunc {
    return (option: any) => {
      const pageInstance = new StatsPageWX(this.$sm, this.appInstance, option)
      pageInstance.init()
      return originPage(pageInstance.option)
    }
  }
  get Component(): WXFunc {
    return (option: any) => {
      const componentInstance = new StatsComponentWX(this.$sm, this.appInstance, option)
      componentInstance.init()
      return originComponent(componentInstance.option)
    }
  }
  get Request(): WXFunc {
    return (option: any) => {
      const $sm = this.$sm
      const modifiedOption = { ...option }
      const urlInfo = this.$sm.$platform?.parseURL(modifiedOption.url)
      const { pathname } = urlInfo
      const $cp = this.appInstance?.currentPageInstance?.$cp
      const pid = this.appInstance?.currentPageInstance?.pid || ''
      const reqStartEventData: EventData = {
        ty: EventDataType.reqStart,
        id: pathname,
        pid,
        t: +new Date()
      }
      const networkData: NetworkData = {
        uri: pathname,
        st: +new Date(),
        req: modifiedOption.data,
        et: -1,
        res: {}
      }
      try {
        if ($cp) {
          $sm.$dm.setData(DataType.Event, reqStartEventData, $cp)
          $sm.$dm.setData(DataType.Network, networkData, $cp)
          $sm.$composer.compose(reqStartEventData, $cp)
        }
      } catch (err) {
        D.log(err)
      }

      modifiedOption.success = function(res: any) {
        try {
          const reqSuccEventData: EventData = {
            ty: EventDataType.reqSucc,
            id: pathname,
            pid,
            t: +new Date()
          }
          // todo 是否需要请求头等信息呢？
          // update networkData by refer
          networkData.res = res.data
          // update succ event after networkData set
          if ($cp) {
            $sm.$dm.setData(DataType.Event, reqSuccEventData, $cp)
            $sm.$composer.compose(reqSuccEventData, $cp)
          }
        } catch (err) {
          D.log(err)
        }
        if (option.success) {
          option.success.call(this, res)
        }
      }
      modifiedOption.fail = function(res: any) {
        try {
          const reqFailEventData: EventData = {
            ty: EventDataType.reqFail,
            id: pathname,
            pid,
            t: +new Date()
          }
          if ($cp) {
            $sm.$dm.setData(DataType.Event, reqFailEventData, $cp)
            $sm.$composer.compose(reqFailEventData, $cp)
          } else {
            throw new Error(ErrCPDoNotExist)
          }
        } catch (err) {
          D.log(err)
        }
        if (option.success) {
          option.success.call(this, res)
        }
      }
      if (option.url.indexOf('.gif') !== -1) {
        return originRequest(option)
      }
      // this.log('Modified Request Exec: ')
      // this.log(option, modifiedOption)
      return originRequest(modifiedOption)
    }
  }
}

export class StatsAppWX {
  private modifiedOption: any
  currentPageInstance?: StatsPageWX
  constructor(private $sm: StatsMain, private originOption: any) {
    this.modifiedOption = { ...this.originOption }
  }
  public originHooks: any
  public modifiedHooks: any
  private _onLaunch: any
  private _onShow: any
  private _onHide: any
  private startTime: any
  init() {
    this.originHooks = { ...this.option }
    this.modifiedHooks = { ...this.originHooks }
    this.modifiedHooks.onLaunch = this.onLaunch
    this.modifiedHooks.onShow = this.onShow
    this.modifiedHooks.onHide = this.onHide
    this.modifiedOption = {
      ...this.modifiedOption,
      ...this.modifiedHooks
    }
  }
  get option() {
    return this.modifiedOption
  }
  setCurrentPage(pi?: StatsPageWX) {
    this.currentPageInstance = pi
  }
  /**
   * 小程序启动事件
   */
  get onLaunch() {
    if (this._onLaunch) {
      return this._onLaunch
    }
    const $this: StatsAppWX = this
    const $sm = this.$sm
    const $originHooks = this.originHooks
    const onLaunch = function(this: any, inputs: any) {
      try {
        $sm.$composer.composeBuiltin({
          name: BuiltInName.AppLaunch,
          pageUUID: inputs.path,
          scene: inputs.scene || ''
        })
      } catch (err) {
        D.error(err)
      }
      if ($originHooks.onLaunch) {
        $originHooks.onLaunch.call(this, inputs)
      }
      D.log('安装小程序（onLaunch）')
    }
    this._onLaunch = onLaunch
    return this._onLaunch
  }
  /**
   * 小程序整体的show事件
   */
  get onShow() {
    if (this._onShow) {
      return this._onShow
    }
    const $this = this
    const $sm = this.$sm
    const $originHooks = this.originHooks
    const onShow = function(this: any, inputs: any) {
      try {
        $sm.$composer.composeBuiltin({
          name: BuiltInName.AppShow,
          pageUUID: inputs.path,
          scene: inputs.scene || ''
        })
        $this.startTime = + new Date
      } catch (err) {
        D.error(err)
      }
      if ($originHooks.onShow) {
        $originHooks.onShow.call(this, inputs)
      }
      D.log('小程序展现（onShow）')
    }
    this._onShow = onShow
    return this._onShow
  }
  /**
   * 小程序进入后台事件
   */
  get onHide() {
    if (this._onHide) {
      return this._onHide
    }
    const $this = this
    const $sm = this.$sm
    const $originHooks = this.originHooks
    const onHide = function(this: any, inputs: any) {

      try {
        const pages = getCurrentPages()
        const route = pages[pages.length - 1]?.route
        // @ts-ignore
        const eventDuration = parseInt((+ new Date - $this.startTime) / 1000)
        $sm.$composer.composeBuiltin({
          name: BuiltInName.AppHide,
          pageUUID: route,
          event_duration: eventDuration || 1
        })
      } catch (err) {
        D.error(err)
      }
      if ($originHooks.onHide) {
        $originHooks.onHide.call(this, inputs)
      }
      D.log('小程序展现（onHide）')
    }
    this._onHide = onHide
    return this._onHide
  }
}

enum TargetVisionStatus {
  Show,
  Hide
}

interface VisionCache {
  [key: string]: { target: any; status: TargetVisionStatus } | undefined
}

export class StatsComponentWX {
  public $cp?: DataPage
  private isAlive: boolean = false
  private isActivated: boolean = false
  private visionCheckTimerID: any = null
  private visionCheckTimeout = 250
  private visionCache: VisionCache = {}
  public originOption: any
  public modifiedOption: any
  public originHooks: any
  public modifiedHooks: any
  private _visionCheck?: () => void
  private _onLoad?: WXFunc
  private _onShow?: WXFunc
  private _onShareAppMessage?: WXFunc
  private _onHide?: WXFunc
  private _onUnload?: WXFunc
  private _eventHandler?: WXFunc
  private startTime: any
  get pid(): string {
    return this.$cp?.pid || ''
  }
  constructor(private $sm: StatsMain, private appInstance: StatsAppWX, option?: any) {
    this.originOption = { ...option }
    this.modifiedOption = { ...this.originOption }
  }
  init() {
    this.originHooks = { ...this.option.methods }
    this.modifiedHooks = { ...this.originHooks }
    this.modifiedHooks.onLoad = this.onLoad
    this.modifiedHooks.onShow = this.onShow
    this.modifiedHooks.onHide = this.onHide
    this.modifiedHooks.onUnload = this.onUnload
    this.modifiedHooks.onShareAppMessage = this.onShareAppMessage
    // uni-app 式的事件统一代理
    if (this.originHooks.__e) {
      const $this = this
      this.modifiedHooks.__e = function(event: any) {
        this.$vm.zsTracker = $this.eventHandler.bind(this, event)
        $this.originHooks.__e.call(this, event)
      }
    }
    this.modifiedOption = {
      ...this.modifiedOption,
      data: {
        ...this.modifiedOption.data,
        zsTracker: this.eventHandler
      },
      methods: this.modifiedHooks
    }
  }
  start(cp: DataPage) {
    this.$cp = cp
    this.isAlive = true
    this.isActivated = true
    this.activate()
  }
  destroy() {
    this.inactivate()
    this.$cp = undefined
    this.isAlive = false
  }
  activate() {
    this.isActivated = true
    this.appInstance?.setCurrentPage(this)
    this.visionCheck()
  }
  inactivate() {
    for (const key in this.visionCache) {
      if (this.visionCache.hasOwnProperty(key)) {
        const item = this.visionCache[key]
        if (item?.status === TargetVisionStatus.Show) {
          const target = item.target
          // D.log('真实展现（hide）', target)
          const eventData: EventData = {
            ty: EventDataType.viewHide,
            id: target.id,
            pid: this.pid,
            t: +new Date(),
            ext: {
              ...target.dataset
            }
          }
          if (this.$cp) {
            this.$sm.$dm.setData(DataType.Event, eventData, this.$cp)
            this.$sm.$composer.compose(eventData, this.$cp)
          } else {
            throw new Error(ErrCPDoNotExist)
          }
        }
      }
    }
    this.visionCache = {}
    this.isActivated = false
    this.clearVisionCheckTimer()
    this.appInstance?.setCurrentPage()
  }
  clearVisionCheckTimer() {
    if (this.visionCheckTimerID !== null) {
      clearTimeout(this.visionCheckTimerID)
    }
  }
  get visionCheck() {
    if (this._visionCheck) return this._visionCheck
    const $this: StatsComponentWX = this
    const $sm = this.$sm
    const check = function(this: any) {
      // 获取在zs sdk page生命周期中的当前page
      const $page = $this.appInstance.currentPageInstance
      if (!$page) {
        return
      }
      const $cp = $page.$cp
      const pid = $this.$cp?.pid
      const pages = getCurrentPages()
      const route = pages[pages.length - 1]?.route
      // D.log('真实展现（debug）', pid, route)
      // D.log('真实展现（debug）', $this.visionCache)
      if (pid && pid === route && $this.isActivated) {
        let suspects = Array.from(
          new Set(
            $sm.$composer
              .getSuspecteds(pid, EventDataType.viewShow)
              .concat($sm.$composer.getSuspecteds(pid, EventDataType.viewHide))
          )
        )
        $sm.$platform?.getVisibleTargets(suspects).then(({ hit, unhit }) => {
          // getVisibleTargets 是异步操作，在回调里需要再次判断页面当前是否active
          // 否则会在离开页面之后也触发viewShow的判定
          if (!$this.isActivated) {
            return
          }
          hit.forEach(target => {
            const targetInCache = $this.visionCache[`${target.id}-${target.zsQueryIndex}`]
            if (targetInCache === undefined || targetInCache.status !== TargetVisionStatus.Show) {
              const eventData: EventData = {
                ty: EventDataType.viewShow,
                id: target.id,
                pid,
                t: +new Date(),
                ext: {
                  ...target.dataset
                }
              }
              if ($cp) {
                $sm.$dm.setData(DataType.Event, eventData, $cp)
                $sm.$composer.compose(eventData, $cp)
              } else {
                throw new Error(ErrCPDoNotExist)
              }

              $this.visionCache[`${target.id}-${target.zsQueryIndex}`] = {
                target,
                status: TargetVisionStatus.Show
              }
            }
          })
          unhit.forEach(target => {
            const targetInCache = $this.visionCache[`${target.id}-${target.zsQueryIndex}`]
            
            if (targetInCache?.status === TargetVisionStatus.Show) {
              // D.log('真实展现（hide）', target)
              const eventData: EventData = {
                ty: EventDataType.viewHide,
                id: target.id,
                pid,
                t: +new Date(),
                ext: {
                  ...target.dataset
                }
              }
              if ($cp) {
                $sm.$dm.setData(DataType.Event, eventData, $cp)
                $sm.$composer.compose(eventData, $cp)
              } else {
                throw new Error(ErrCPDoNotExist)
              }
              $this.visionCache[`${target.id}-${target.zsQueryIndex}`] = {
                target,
                status: TargetVisionStatus.Hide
              }
            }
          })
        })
      } else {
        D.log('异步的隐藏判断，上下文丢失了，用缓存上下文操作')
      }
      $this.visionCheckTimerID = setTimeout(check, $this.visionCheckTimeout)
    }
    const visionCheck = function(this: any) {
      $this.clearVisionCheckTimer()
      check()
    }
    this._visionCheck = visionCheck
    return visionCheck
  }
  get onLoad() {
    if (this._onLoad) return this._onLoad
    const $this: StatsComponentWX = this
    const $sm = this.$sm
    const $originHooks = this.originHooks
    const onLoad = function(this: any, inputs: any) {
      try {
        if (!$this.isActivated) {
          const cp = $this.$sm.$dm.appendDataPage(this.route)
          $sm.$dm.setData(DataType.Context, inputs, cp)
          $this.startTime = + new Date
          $sm.$composer.composeBuiltin({
            name: BuiltInName.PageShow
          }, cp)
          $this.start(cp)
        }
      } catch (err) {
        D.error(err)
      }
      if ($originHooks.onLoad) {
        $originHooks.onLoad.call(this, inputs)
      }
      D.log('页面进入（onLoad）', this.route)
    }
    this._onLoad = onLoad
    return onLoad
  }
  get onShow() {
    if (this._onShow) return this._onShow
    const $this: StatsComponentWX = this
    const $sm = this.$sm
    const $originHooks = this.originHooks
    const onShow = function(this: any, inputs: any) {
      try {
        if (!$this.isActivated && $this.isAlive) {
          if ($this.$cp) {
            $sm.$dm.setData(DataType.Context, inputs, $this.$cp)
            $this.startTime = + new Date
            $sm.$composer.composeBuiltin({
              name: BuiltInName.PageShow
            }, $this.$cp)
          } else {
            throw new Error(ErrCPDoNotExist)
          }
          $this.activate()
        }
      } catch (err) {
        D.error(err)
      }
      if ($originHooks.onShow) {
        $originHooks.onShow.call(this, inputs)
      }
      D.log('页面进入（onShow）', this.route)
    }
    this._onShow = onShow
    return onShow
  }
  get onHide() {
    if (this._onHide) return this._onHide
    const $this: StatsComponentWX = this
    const $sm = this.$sm
    const $originHooks = this.originHooks
    const onHide = function(this: any, inputs: any) {
      try {
        if ($this.isActivated && $this.isAlive) {
          if ($this.$cp) {
            // @ts-ignore
            const eventDuration = parseInt((+ new Date - $this.startTime) / 1000)
            $sm.$dm.setData(DataType.Context, inputs, $this.$cp)
            $sm.$composer.composeBuiltin({
              name: BuiltInName.PageHide,
              event_duration: eventDuration || 1
            }, $this.$cp)
          } else {
            throw new Error(ErrCPDoNotExist)
          }
          $this.inactivate()
        }
      } catch (err) {
        D.error(err)
      }
      if ($originHooks.onHide) {
        $originHooks.onHide.call(this, inputs)
      }
      D.log('页面离开（onHide）', this.route)
    }
    this._onHide = onHide
    return onHide
  }
  get onUnload() {
    if (this._onUnload) return this._onUnload
    const $this: StatsComponentWX = this
    const $sm = this.$sm
    const $originHooks = this.originHooks
    const onUnload = function(this: any, inputs: any) {
      try {
        if ($this.isActivated && $this.isAlive) {
          if ($this.$cp) {
            // @ts-ignore
            const eventDuration = parseInt((+ new Date - $this.startTime) / 1000)
            $sm.$dm.setData(DataType.Context, inputs, $this.$cp)
            $sm.$composer.composeBuiltin({
              name: BuiltInName.PageHide,
              event_duration: eventDuration || 1
            }, $this.$cp)
          } else {
            throw new Error(ErrCPDoNotExist)
          }
        }
        $this.destroy()
      } catch (err) {
        D.error(err)
      }
      if ($originHooks.onUnload) {
        $originHooks.onUnload.call(this, inputs)
      }
      D.log('页面离开（onUnload）', this.route)
    }
    this._onUnload = onUnload
    return onUnload
  }
  get onShareAppMessage() {
    if (this._onShareAppMessage) return this._onShareAppMessage
    const $this: StatsComponentWX = this
    const $sm = this.$sm
    const $originHooks = this.originHooks
    const onShareAppMessage = function(this: any, inputs: any) {
      try {
        if ($this.isActivated && $this.isAlive) {
          if ($this.$cp) {
            const shareEventData: EventData = {
              ty: EventDataType.share,
              pid: this.route,
              id: "$share",
              t: +new Date()
            }
            $sm.$dm.setData(DataType.Event, shareEventData, $this.$cp)
            $sm.$composer.compose(shareEventData, $this.$cp)
          } else {
            throw new Error(ErrCPDoNotExist)
          }
          $this.activate()
        }
      } catch (err) {
        D.error(err)
      }
      let  resultValue = {}
      if ($originHooks.onShareAppMessage) {
        resultValue = $originHooks.onShareAppMessage.call(this, inputs)
      }
      D.log('触发分享', this.route)
      return resultValue
    }
    this._onShareAppMessage = onShareAppMessage
    return onShareAppMessage
  }
  get eventHandler() {
    if (this._eventHandler) return this._eventHandler
    const $this: StatsComponentWX = this
    const $sm = this.$sm
    const eventHandler = function(this: any, event: any) {
      try {
        const $page = $this.appInstance.currentPageInstance
        if (!$page) {
          return
        }
        const pid = $page.$cp?.pid || ''
        const { target, type, detail } = event
        const { x, y } = detail
        let innerType = type
        switch (type) {
          case 'tap':
            innerType = EventDataType.click
            break
          default:
            break
        }
        if (type === 'tap') {
          let suspects = $sm.$composer.getSuspecteds(pid, innerType)
          let ctx: any = this
          $sm.$platform?.getHitTargets(x, y, suspects, ctx, target).then(targets => {
            targets.forEach(target => {
              const ext = {
                ...target.dataset
              }
              try {
                if (ext.zsIdx !== undefined && ext.zsList !== undefined) {
                  ext.zsItem = ext.zsList[ext.zsIdx] || {}
                }
              } catch (e) {
                D.log(e)
              }

              const eventData: EventData = {
                ty: EventDataType.click,
                id: target.id,
                pid,
                t: +new Date(),
                ext
              }
              if ($page.$cp) {
                $sm.$dm.setData(DataType.Event, eventData, $page.$cp)
                $sm.$composer.compose(eventData, $page.$cp)
              } else {
                throw new Error(ErrCPDoNotExist)
              }
            })
          })
        }
        if (type === 'longpress') {
          wx.setClipboardData({
            data: $sm.$dm.$global.i,
            success() {
              wx.getClipboardData({
                success(res: any) {
                  console.log('复制：', res.data) // data
                }
              })
            }
          })
        }
      } catch (err) {
        D.error(err)
      }
    }
    this._eventHandler = eventHandler
    return eventHandler
  }
  get option() {
    return this.modifiedOption
  }
}

export class StatsPageWX extends StatsComponentWX {
  init() {
    this.originHooks = { ...this.option }
    this.modifiedHooks = { ...this.originHooks }
    this.modifiedHooks.onLoad = this.onLoad
    this.modifiedHooks.onShow = this.onShow
    this.modifiedHooks.onHide = this.onHide
    this.modifiedHooks.onUnload = this.onUnload
    this.modifiedHooks.onShareAppMessage = this.onShareAppMessage
    this.modifiedOption = {
      ...this.modifiedOption,
      ...this.modifiedHooks
    }
  }
}
