import {
  DataPool,
  GlobalData,
  DeviceType,
  NetworkStatusType,
  DataDescriber,
  DataType,
  EventData,
  NetworkData,
  DataPage,
  ContextsDataPool,
  Key,
  EventDataType,
  ActType
} from '@zyb-data/stats-core'
import { StatsConfigService } from './config'
import { D } from './debug'

/**
 * 全局数据默认值
 */
const defaultGlobalData: GlobalData = {
  paid: '-1',
  un: '',
  i: '',
  fr: DeviceType.others,
  mc: '',
  sv: '',
  s: '',
  l: NetworkStatusType.Others,
  channel: '',
  av: '',
  op: '',
  sdkVersion: '',
  ip: '',
  plat: '',
  pageUUID: '',
  act: ActType.default
}

/**
 * 数据值的合并类型
 */
type DataValue = EventData | NetworkData | { [paramsName: string]: any } | any

/**
 * 数据池管理器
 */
export class StatsDataPoolManager {
  /**
   * 数据池
   */
  private dataPool: DataPool

  /**
   *
   * @param $config 配置服务
   */
  constructor(private $config: StatsConfigService) {
    this.dataPool = {
      gl: { ...defaultGlobalData },
      dp: []
    }
  }

  /**
   * 插入新页面数据
   * @param pid 页面id
   */
  appendDataPage(pid: string = ''): DataPage {
    const dp = this.createDataPage(pid)
    this.$dp.push(dp)
    return dp
  }

  /**
   * 创建页面数据
   * @param pid 页面id
   */
  createDataPage(pid: string = ''): DataPage {
    let dataPage: DataPage = {
      pid: pid,
      st: +new Date(),
      et: 0,
      events: new Map(),
      networks: new Map(),
      contexts: {}
    }
    return dataPage
  }

  /**
   * 获取页面数据
   * @param lb lookback 回溯层级 @default 0
   */
  getDataPage(lb: number = 0): DataPage {
    return this.dataPool.dp[this.dataPool.dp.length - 1 - lb]
  }

  /**
   * 获取当前页面数据
   */
  getCurrentDataPage(): DataPage {
    return this.getDataPage()
  }

  /**
   * 设置数据
   * @param dataType 数据类型
   * @param value 数据值
   * @param cp 当前页面上下文数据
   */
  setData(dataType: DataType, value: DataValue, cp: DataPage): DataValue {
    switch (dataType) {
      case DataType.Global:
        this.$global = { ...this.$global, ...value }
        break
      case DataType.Context:
        cp.contexts = { ...cp.contexts, ...value }
        break
      case DataType.Event:
        const { ty, id } = value as EventData
        if (!cp.events.get(ty)) {
          cp.events.set(ty, new Map())
        }
        if (!cp.events.get(ty)?.get(id)) {
          cp.events.get(ty)?.set(id, [])
        }
        cp.events
          .get(ty)
          ?.get(id)
          ?.push(value)
        break
      case DataType.Network:
        const { uri } = value as NetworkData
        if (!cp.networks.get(uri)) {
          cp.networks.set(uri, [])
        }
        cp.networks.get(uri)?.push(value)
        break
      default:
        D.error('Invalid DataType.')
    }
    return value
  }

  /**
   * 获取数据
   * @param dd 数据描述
   * @param cp 当前页面上下文数据
   */
  getData(dd: DataDescriber, cp?: DataPage): DataValue {
    const { ty, lb = 0, et, eid, u = '', ks = [] } = dd
    cp = cp || this.getDataPage(lb)
    switch (ty) {
      case DataType.Global:
        return this.access(this.$global, ks)
      case DataType.Context:
        return this.access(cp.contexts, ks)
      case DataType.Event:
        return this.access(this.searchEvent(cp, et!, eid), ks)
      case DataType.Network:
        return this.access(this.searchNetwork(cp, u), ks)
      default:
        return undefined
    }
  }

  getPool(): DataPool {
    return this.dataPool
  }

  get $dp(): Array<DataPage> {
    return this.dataPool.dp
  }

  get $global(): GlobalData {
    return this.dataPool.gl
  }

  set $global(gl: GlobalData) {
    this.dataPool.gl = { ...this.dataPool.gl, ...gl }
  }

  /**
   * 设置全局数据
   * @param data kv
   */
  setGlobal(data: any) {
    this.dataPool.gl = { ...this.dataPool.gl, ...data }
  }

  get $common() {
    return {}
  }

  get $currentPage(): DataPage {
    return this.getCurrentDataPage()
  }

  /**
   * 对obj进行任意层级的安全取值方法
   *
   * @param obj 目标对象
   * @param ks keys
   */
  private access(obj: any, ks: Array<Key> = []): any {
    const defaultValue = ''
    if (!obj) {
      return defaultValue
    }
    let iter: any = obj
    for (let i = 0; i < ks.length; i++) {
      const { t, v } = ks[i]
      // todo 区分object和数组
      iter = iter[v]
      if (iter === undefined) {
        break
      }
    }
    return iter === undefined ? defaultValue : iter
  }

  // 暂时没用到
  private searchEvents(cp: DataPage, ty: EventDataType, eid?: string): Array<EventData> {
    if (eid) {
      let results = cp.events.get(ty)?.get(eid)
      return results || []
    }
    // todo 模糊搜索支持
    return []
  }

  private searchEvent(cp: DataPage, ty: EventDataType, eid?: string): EventData | undefined {
    const list = this.searchEvents(cp, ty, eid)
    return list[list.length - 1]
  }

  private searchNetworks(cp: DataPage, u: string): Array<NetworkData> {
    let results = cp.networks.get(u)
    return results || []
  }

  private searchNetwork(cp: DataPage, u: string): NetworkData | undefined {
    const list = this.searchNetworks(cp, u)
    return list[list.length - 1]
  }
}
