export interface StatsConfig {
  /**
   * sdk分配给业务的唯一标识，用于匹配配置文件
   */
  zpID?: string
  /**
   * 业务plat
   */
  plat?: string
  /**
   * 上报地址
   */
  logUrl?: string
  /**
   * 运行环境
   */
  env?: string
  /**
   * 打开调试
   */
  debug?: boolean
  /**
   * 使用本地规则调试
   */
  useLocalRules?: boolean
  /**
   * 内置统计项开关
   */
  buildInTraking?: boolean
}

/**
 * 环境值
 */
export enum Env {
  Dev = 'development',
  Prod = 'production',
  Gray = 'gray'
}

/**
 * 配置共享池
 */
export class StatsConfigService {
  /**
   *
   * @param config
   */
  constructor(private config: StatsConfig) {}

  get(): StatsConfig {
    return this.config
  }

  set(config: StatsConfig) {
    this.config = { ...this.config, ...config }
  }
}
