import { TestRule } from './dev-rules'
import {
  ProjectConfigDetail,
  Rule,
  DataType,
  EventDataType,
  EventData,
  Comparator,
  ActType,
  DataPage,
  Value,
  ValueType,
  BuiltInName
} from '@zyb-data/stats-core'
import { StatsPlatform } from './platform'
import { D } from './debug'
import { StatsDataPoolManager } from './data-pool'
import { StatsLogger } from './logger'
import { StatsConfigService, Env } from './config'
import { StatsBuildInLogNames } from './const'
import { ready, StatsReady } from './ready'

/**
 * 规则索引表
 * 为提高规则匹配效率和减少不必要的数据进入数据池
 * 以页面和事件类型作为一二级索引
 * 能获取需要进行检查的事件id和对应id需要进行判断的规则列表
 */
interface StatasRulesMap {
  [pid: string]: {
    [et: string]: {
      // 用于过滤事件的id列表
      ids: Set<string>
      // id及id涉及的规则列表的映射
      rules: Map<string, Array<Rule>>
    }
  }
}

interface BuiltInData {
  name: BuiltInName
  pageUUID?: string
  scene?: number
  event_duration?: number
}

/**
 * 规则本地存储key
 */
const CachedRuleStoreKey = 'cache_rules'

/**
 * StatsComposer
 * 规则处理器
 */
export class StatsComposer {
  // @ts-ignore
  configDetails: ProjectConfigDetail = TestRule
  /**
   * 预处理后的规则索引表
   */
  rulesMap: StatasRulesMap = {}

  /**
   * ready 方法，一些地方需要等composer ready
   */
  composerReady: StatsReady = new StatsReady()
  /**
   *
   * @param platform 平台实例
   * @param dm 数据服务
   * @param logger 日志服务
   * @param config 配置服务
   */
  constructor(
    private platform: StatsPlatform,
    private dm: StatsDataPoolManager,
    private logger: StatsLogger,
    private config: StatsConfigService
  ) {
    // 如果开启了使用本地调试的话，直接初始化本地测试规则
    if (this.config.get().useLocalRules) {
      // @ts-ignore
      this.rulesMap = this.preprocessRules(TestRule)
      D.log('Mock 配置索引', this.rulesMap)
    }
  }

  get ready() {
    return this.composerReady.ready.bind(this.composerReady)
  }

  /**
   * 更新配置
   */
  update() {
    // 如果开启了使用本地调试的话，不请求远程的配置文件，使用项目内的testrule调试
    if (this.config.get().useLocalRules) {
      // @ts-ignore
      this.rulesMap = this.preprocessRules(TestRule)
      this.composerReady.init()
      D.log('Mock 配置索引', this.rulesMap)
      return
    }
    // 获取配置概要，只包含当前最新配置文件md5和下载地址，不包含配置内容
    this.platform
      .request({
        url: `https://zyb-stats.zuoyebang.cc/stats/${this.config.get().zpID}${
          this.config.get().env === Env.Dev ? '.test' : ''
        }.json?t=${+new Date()}`
      })
      .then(
        res => {
          const configFileUrl = res?.data?.fileName
          const md5 = res.data.md5
          if (!configFileUrl || !md5) {
            D.error('配置未发布')
            return
          }
          // 尝试获取缓存配置，并比较md5
          const cachedRuleStr = this.platform.getStore(CachedRuleStoreKey)
          if (cachedRuleStr) {
            try {
              const cachedRule = JSON.parse(cachedRuleStr)
              if (cachedRule && cachedRule.md5 === md5) {
                this.rulesMap = this.preprocessRules(cachedRule.data)
                D.sys('已加载并使用缓存配置', this.rulesMap)
                this.composerReady.init()
                return
              }
            } catch (e) {
              // 无有效缓存
            }
          }
          // 根据md5及下载地址，获取远程完整网络配置，并缓存
          this.platform
            .request({
              url: configFileUrl
            })
            .then(
              res => {
                this.rulesMap = this.preprocessRules(res.data)
                D.sys('已加载并使用远程配置', this.rulesMap)
                this.platform.setStore(
                  CachedRuleStoreKey,
                  JSON.stringify({
                    md5,
                    data: res.data
                  })
                )
                D.sys('配置已缓存')
                this.composerReady.init()
              },
              err => {
                D.error('获取配置文件失败', err)
              }
            )
        },
        err => {
          D.error('获取配置md5失败', err)
        }
      )
  }

  /**
   * 获取可能命中规则的id列表
   * @param pid 页面id
   * @param et 事件类型
   */
  getSuspecteds(pid: string, et: string): string[] {
    let result = this.rulesMap[pid]?.[et]

    const ids = result?.ids || []
    return Array.from(ids)
  }

  /**
   * 判断事件id是否可能命中规则
   * @param pid 页面id
   * @param et 事件类型
   * @param eid 事件id
   */
  isSuspected(pid: string, et: string, eid: string) {
    return this.rulesMap[pid]?.[et]?.ids.has(eid)
  }

  /**
   * 处理内置事件
   * 内置事件无需匹配规则
   * @param data 内置事件参数
   * @param cp 页面数据上下文
   */
  composeBuiltin(data: BuiltInData, cp?: DataPage) {
    if (!this.config.get().buildInTraking) {
      return
    }
    const t = +new Date()
    ready(() => {
      const d = this.logger.send({
        ...this.dm.$global,
        ...this.dm.$common,
        pageUUID: cp?.pid || '',
        act: ActType.view,
        t,
        ...data
      })
      D.info(data.name, '内置统计', d)
    })
  }

  /**
   * 处理事件数据，进行规则匹配
   * 遍历所有命中规则进行匹配处理
   * @param data 事件数据
   * @param cp 产生当前事件的页面数据上下文
   */
  compose(data: EventData, cp: DataPage) {
    const { ty, t, id, pid } = data
    // 必须要等规则加载完以后才能触发对比
    this.ready(() => {
      const rules = this.rulesMap[pid]?.[ty]?.rules?.get(id)

      if (rules) {
        D.group('Compose ')
        D.log(`命中事件、当前数据池、当前待匹配规则 `, data, cp, rules)
        D.log(`获取规则 this.rulesMap[${pid}]?.[${ty}]?.rules?.get(${id})`)
        // 已经通过规则索引表索引到对应的规则
        // 当前事件数据已符合rules列表中的规则的主要条件
        // 遍历所有命中主条件被命中的规则，进行进一步的检查
        rules.forEach(rule => {
          this.handleRule(data, rule, cp)
        })
      }
      rules && D.groupEnd()
    })
  }

  /**
   * 单条规则的匹配
   * @param data 事件数据
   * @param rule 规则
   * @param cp 产生当前事件的页面数据上下文
   */
  private handleRule(data: EventData, rule: Rule, cp: DataPage) {
    const { ty, t, id, ext = {}, pid } = data
    const conditions = rule.cds
    const fields = rule.fs
    const name = rule.ln
    const lnc = rule.lnc
    const act = rule.act
    let isFullyMatch = true
    // 匹配其他条件
    for (let i = 0; i < conditions.length; i++) {
      const condition = conditions[i]
      isFullyMatch = this.compare(this.dm.getData(condition.tar, cp), condition.s, condition.c)
      D.log(`条件：${JSON.stringify(condition)}, 匹配结果：${isFullyMatch}`)
      // 任意条件不匹配则不命中本规则
      if (!isFullyMatch) {
        break
      }
    }
    // 完全命中规则，开始取上报业务字段，进行参数整理
    if (isFullyMatch) {
      let params: { [paramName: string]: any } = {}
      fields.forEach(filed => {
        params[filed.to] = this.dm.getData(filed.fr, cp)
      })
      let d = this.logger.send({
        ...this.dm.$global,
        ...this.dm.$common,
        name,
        t,
        lnc,
        act,
        pageUUID: cp.pid,
        ...params
      })
      D.info(name, lnc, d)
    }
  }

  /**
   * 比较方法
   * @param a 值a
   * @param b 值b，值b会声明类型，详见 {@link Value} 类型
   * @param c 比较符
   */
  private compare(a: any, b: Value, c: Comparator) {
    D.log(`COMPARE: (${a}) [${c}] (${b.v} as ${b.t})`)
    switch (b.t) {
      case ValueType.Number:
        a = +a
        break
      case ValueType.String:
        a = '' + a
        break
      default:
        break
    }
    const v = b.v
    switch (c) {
      case Comparator.Equal:
        return a === v
      case Comparator.NEQ:
        return a !== v
      case Comparator.GT:
        return a > v
      case Comparator.LT:
        return a < v
      case Comparator.Like:
        return a?.indexOf(v) !== -1
      case Comparator.Exist:
        return a !== undefined && a !== ''
      default:
        return false
    }
  }

  /**
   * 预处理规则，以提高规则索引速度和提供数据池过滤依据
   *
   * @param configs 远程配置完整内容
   */
  private preprocessRules(configs: ProjectConfigDetail): StatasRulesMap {
    let rulesMap: StatasRulesMap = {}
    configs.rules.forEach((r: Rule) => {
      // 待push到索引表中的id集合
      const idCollections: { et?: EventDataType; eid?: string }[] = []
      idCollections.push({ et: r.et, eid: r.eid })

      // 抽取条件中覆盖到的元素id和网络请求id
      r.cds.forEach(cd => {
        try {
          const tar = cd.tar
          if (tar.ty === DataType.Event) {
            idCollections.push({ et: tar.et, eid: tar.eid })
          }
          if (tar.ty === DataType.Network) {
            idCollections.push({ et: EventDataType.reqStart, eid: tar.u })
            idCollections.push({ et: EventDataType.reqSucc, eid: tar.u })
            idCollections.push({ et: EventDataType.reqFail, eid: tar.u })
          }
        } catch (err) {
          D.error(err, r)
        }
      })
      // 抽取字段声明中覆盖到的元素id和网络请求id
      r.fs.forEach(f => {
        try {
          const tar = f.fr
          if (tar.ty === DataType.Event) {
            idCollections.push({ et: tar.et, eid: tar.eid })
          }
          if (tar.ty === DataType.Network) {
            idCollections.push({ et: EventDataType.reqStart, eid: tar.u })
            idCollections.push({ et: EventDataType.reqSucc, eid: tar.u })
            idCollections.push({ et: EventDataType.reqFail, eid: tar.u })
          }
        } catch (err) {
          D.error(err, r)
        }
      })
      // 以页面、事件类型两个维度，重新组织数据
      r.ps.forEach(p => {
        if (!rulesMap[p]) {
          rulesMap[p] = {}
        }
        if (!rulesMap[p][r.et]) {
          rulesMap[p][r.et] = {
            ids: new Set<string>(),
            rules: new Map<string, Array<Rule>>()
          }
        }
        if (!rulesMap[p][r.et].rules.get(r.eid)) {
          rulesMap[p][r.et].rules.set(r.eid, [])
        }
        rulesMap[p][r.et].rules.get(r.eid)?.push(r)
        idCollections.forEach(kv => {
          if (!kv.et || !kv.eid) {
            return
          }
          if (!rulesMap[p][kv.et]) {
            rulesMap[p][kv.et] = {
              ids: new Set<string>(),
              rules: new Map<string, Array<Rule>>()
            }
          }
          rulesMap[p][kv.et].ids.add(kv.eid)
        })
      })
    })
    return rulesMap
  }
}
