/// <reference path="../../typings/mp/index.d.ts" />

import StatsMain from '../core'
import { StatsPlatformMp } from './platform'
import { D } from '../core/debug'
import { StatsConfig, Env } from '../core/config'
import { version } from './version'
import { StatsAppFactoryWX } from './page'
const sdkVersion = version

export interface ZybStatsWXConfig extends StatsConfig {}

const logUrlDev = 'https://nlogtj.zuoyebang.cc/nlogtj/xiaochengxu_test'
const logUrlProd = 'https://nlogtj.zuoyebang.cc/nlogtj/xiaochengxu'

class ZybStatsWX {
  private cfg: ZybStatsWXConfig = {}
  private sm: StatsMain = new StatsMain({ platform: new StatsPlatformMp() }, this.cfg)
  constructor() {
    const appFactory = new StatsAppFactoryWX(this.sm)
    appFactory.init()
  }
  /**
   * 更新配置
   * @param config
   */
  public config(config: ZybStatsWXConfig) {
    // 小程序的上报地址控制收敛到小程序sdk入口这儿
    if (!config.logUrl) {
      switch (config.env) {
        case Env.Dev:
          config.logUrl = logUrlDev
          break
        case Env.Prod:
          config.logUrl = logUrlProd
          break
        default:
          config.logUrl = logUrlProd
          break
      }
    }
    this.cfg = { ...this.cfg, ...config }
    this.sm.$config.set(config)
    this.sm.$dm.setGlobal({ plat: this.cfg.plat, zpID: this.cfg.zpID, sdkVersion })
  }
  /**
   * 设置、更新全局变量
   * @param params
   */
  public global(params: { [key: string]: any }) {
    this.sm.$dm.setGlobal(params)
  }
  /**
   * 设置openid
   * @param openid
   */
  public setOpenid(openid: string, updateStore: boolean = true) {
    this.global({ i: openid })
    if (updateStore) {
      this.sm.$platform?.setStore('openid', openid)
    }
  }
  /**
   * 设置unionid
   * @param unionid
   */
  public setUnionid(unionid: string, updateStore: boolean = true) {
    this.global({ mp_unionid: unionid })
    if (updateStore) {
      this.sm.$platform?.setStore('unionid', unionid)
    }
  }
  public setAppid(appid: string, updateStore: boolean = true) {
    this.global({ mp_appid: appid })
  }
  /**
   * 设置登陆用户id（作业帮用户id，加密）
   * @param wxuid
   */
  public login(wxuid: string = '-1', updateStore: boolean = true) {
    this.global({ paid: wxuid })
    if (updateStore) {
      this.sm.$platform?.setStore('wxuid', wxuid)
    }
  }
  /**
   * 初始化
   */
  public init() {
    // 挂载到wx上
    wx.__zs = this
    wx.zs = this
    wx.zsd = D
    D.log('全埋点SDK初始化')
    this.sm.init()
    const platform = this.sm.$platform!
    const openid = platform.getStore('openid')
    const unionid = platform.getStore('unionid')
    const wxuid = platform.getStore('wxuid')
    if (openid) {
      this.setOpenid(openid, false)
    }
    if (unionid) {
      this.setUnionid(unionid, false)
    }
    if (wxuid) {
      this.login(wxuid, false)
    }
  }
  /**
   * 主动埋点方法
   * @param name 点位名字
   * @param params 点位参数
   */
  public track(name: string, params: object) {
    return this.sm.track(name, params)
  }

  /**
   * 获取cuid
   */
  public getCuid() {
    return this.sm.$platform?.getCUID()
  }
}

export const zs = new ZybStatsWX()
