import { StatsPlatform } from './platform'
import { StatsConfigService } from './config'
import { D } from './debug'

export class StatsLogger {
  constructor(private $platform: StatsPlatform, private $config: StatsConfigService) {}
  send(data: any): any {
    if (this.$config.get().debug) {
      D.pushRecord(data)
    }
    this.$platform
      .sendLog({
        url: this.$config.get().logUrl,
        data: data
      })
      .then(
        () => {},
        () => {}
      )
    return data
  }
}
