import { StatsDataPoolManager } from './data-pool'
import { StatsComposer } from './composer'
import { StatsPlatform } from './platform'
import { StatsLogger } from './logger'
import { StatsConfigService, StatsConfig } from './config'
import { statsReady } from './ready'

/**
 * sdk core 主类option
 */
export interface StatsMainOption {
  platform?: StatsPlatform
}

/**
 * sdk core 主类
 */
export default class StatsMain {
  /**
   * 数据服务
   */
  private dataPoolManager: StatsDataPoolManager
  /**
   * 逻辑处理器
   */
  private composer: StatsComposer
  /**
   * 平台实例
   */
  private platform?: StatsPlatform
  /**
   * 日志服务
   */
  private logger: StatsLogger
  /**
   * 配置服务
   */
  private config: StatsConfigService = new StatsConfigService({
    debug: true,
    buildInTraking: true
  })
  constructor(options: StatsMainOption, config: StatsConfig) {
    if (options.platform) {
      this.platform = options.platform
    }
    this.dataPoolManager = new StatsDataPoolManager(this.config)
    this.logger = new StatsLogger(this.platform!, this.config)
    this.composer = new StatsComposer(
      this.platform!,
      this.dataPoolManager,
      this.logger,
      this.config
    )
    this.config.set(config)
  }
  /**
   * 获取处理器
   */
  get $composer() {
    return this.composer
  }
  /**
   * 获取数据管理器
   */
  get $dm() {
    return this.dataPoolManager
  }
  /**
   * 获取平台实例
   */
  get $platform() {
    return this.platform
  }
  /**
   * 获取日志服务
   */
  get $logger() {
    return this.logger
  }
  /**
   * 获取全局配置
   */
  get $config() {
    return this.config
  }
  /**
   * 初始化sdk core
   * @param cb success callback
   */
  init(cb?: () => void) {
    // 获取初始化sdk以来的相关数据
    // 这些数据在某些平台是同步的，某些平台可能是异步的
    // 具体看平台实例的实现
    return Promise.all([
      this.$platform?.getSystemInfo(),
      this.$platform?.getNetworkInfo(),
      this.$platform?.getCUID()
    ])
      .then(([system, network, cuid]) => {
        this.$dm.setGlobal({
          mc: system?.brand,
          sv: system?.system?.split(' ')[1],
          s: `${system.screenWidth}*${system.screenHeight}`,
          fr: system?.system?.split(' ')[0],
          l: network.networkType,
          i: cuid,
          av: system.browser
        })
        // 更新规则文件
        if (this.config.get().zpID) {
          this.$composer.update()
        }
        // sdk已经初始化，触发ready里存的callback
        statsReady.init()
        cb && cb()
      })
      .catch(e => {
        console.log('zs SDK init error')
      })
  }
  /**
   * 手动打点接口
   * @param name 点位名
   * @param params 点位业务参数
   */
  track(name: string, params: object) {
    this.$logger.send({ ...this.$dm.$global, ...this.$dm.$common, name, ...params })
  }
}
