import { StatsPlatform } from '../core/platform'
import SparkMD5 from 'spark-md5'
import { D } from '../core/debug'
import { parse } from 'url'
const originRequest = wx.request

export class StatsPlatformMp implements StatsPlatform {
  private systemInfo: any
  private networkInfo: any
  private storePrefix = '__zs_'
  private cuidStoreKey = 'cuid'
  parseURL(url: string): any {
    return parse(url)
  }
  getCUID(): Promise<string> {
    return new Promise((resolve, reject) => {
      if (this.getStore('openid')) {
        resolve(this.getStore('openid'))
        return
      }
      if (this.getStore(this.cuidStoreKey)) {
        resolve(this.getStore(this.cuidStoreKey))
        return
      }
      if (this.systemInfo) {
        resolve(this.cuid())
      } else {
        this.getSystemInfo().then(
          info => {
            resolve(this.cuid())
          },
          () => {
            resolve(this.cuid())
          }
        )
      }
    })
  }
  private cuid(): string {
    let factors: any[] = ['wechat', 'miniProgram', 'data-stats', '750', '1240']
    if (this.systemInfo) {
      factors[3] = this.systemInfo.screenWidth
      factors[4] = this.systemInfo.screenHeight
    }
    factors = factors.concat([+new Date(), Math.random()])
    const cuid = SparkMD5.hashBinary(factors.join('-')).substr(0, 32)
    this.setStore(this.cuidStoreKey, cuid)
    return cuid
  }
  getNetworkInfo(): Promise<any> {
    return new Promise((resolve, reject) => {
      let $this = this
      wx.getNetworkType({
        success(res: any) {
          D.log('getNetworkInfo', res)
          $this.networkInfo = res
          resolve(res)
        }
      })
    })
  }
  getSystemInfo(): Promise<any> {
    return new Promise((resolve, reject) => {
      let $this = this
      wx.getSystemInfo({
        success(res: any) {
          $this.systemInfo = res
          resolve(res)
        }
      })
    })
  }
  request(option: any): Promise<any> {
    return new Promise((resolve, reject) => {
      option.success = function(res: any) {
        resolve(res)
      }
      option.fail = function(err: any) {
        reject(err)
      }
      originRequest(option)
    })
  }
  sendLog(option: any): Promise<any> {
    return new Promise((resolve, reject) => {
      option.success = function(res: any) {
        resolve(res)
      }
      option.fail = function(err: any) {
        reject(err)
      }
      originRequest(option)
    })
  }
  setStore(key: string, value: any): any {
    return wx.setStorageSync(`${this.storePrefix}${key}`, value)
  }
  getStore(key: string): any {
    return wx.getStorageSync(`${this.storePrefix}${key}`)
  }
  getHitTargets(x: number, y: number, elements: string[], ctx: any, target: any): Promise<any[]> {
    elements = elements.map(v => `#${v}`)
    return new Promise(resolve => {
      let targets: any = []

      // 先判断target是否有id，如果有id就不做后续处理了
      if (target.id && elements.indexOf(`#${target.id}`) > -1) {
        resolve([target])
        return
      }

      Promise.all([
        this.getViewportInfo(),
        Promise.all(
          elements.map((element, idx) => {
            return this.getBoundingClientRect(element, ctx, true)
          })
        )
      ]).then(resp => {
        const viewport = resp[0]
        const { scrollOffset } = viewport
        const { scrollTop } = scrollOffset

        const rectsArr = resp[1]
        rectsArr.forEach((rects:any) => {
          rects.forEach((rect: any) => {
            if (this.isPointInTarget(x, y, rect, scrollTop)) {
              targets.push(rect)
            }
          })
        })
        
        resolve(targets)
      })
    })
  }
  getVisibleTargets(elements: string[]): Promise<{ hit: any[]; unhit: any[]; list: any[] }> {
    elements = elements.map(v => `#${v}`)
    elements = elements.concat(elements.map(v => `.zyb-stats >>> ${v}`))
    return new Promise(resolve => {
      let targets: any[] = []
      let unhit: any[] = []
      this.getViewportInfo().then(
        viewport => {
          const viewportRect = viewport.rect
          Promise.all(
            elements.map((element, idx) => {
              return this.getBoundingClientRect(element)
            })
          ).then(
            rectsArr => {
              rectsArr.forEach(rects => {
                rects.forEach(rect => {
                  if (this.isTargetInViewport(rect, viewportRect)) {
                    targets.push(rect)
                  } else {
                    unhit.push(rect)
                  }
                })
              })
              resolve({ hit: targets, unhit, list: rectsArr })
            },
            () => {
              // todo
            }
          )
        },
        () => {
          // todo
        }
      )
    })
  }
  private getBoundingClientRect(element: string, ctx?: any, log?: boolean): Promise<any[]> {
    return new Promise(reslove => {
      let query = wx.createSelectorQuery()
      if (ctx) {
        query = query.in(ctx)
      }
      query.selectAll(element).boundingClientRect()
      query.exec((res: any) => {
        reslove(
          res[0].map((v: any, i: number) => {
            return {
              ...v,
              zsQueryIndex: i
            }
          })
        )
      })
    })
  }
  private getViewportInfo(): Promise<any> {
    return new Promise(reslove => {
      const query = wx.createSelectorQuery()
      query.selectViewport().boundingClientRect()
      query.selectViewport().scrollOffset()
      query.exec((res: any) => {
        reslove({ rect: res[0], scrollOffset: res[1] })
      })
    })
  }
  private isPointInTarget(x: number, y: number, rect: any, scrollTop: number): boolean {
    if (!rect) return false
    const { left, right, top, height } = rect
    if (left < x && x < right && scrollTop + top < y && y < scrollTop + top + height) {
      return true
    }
    return false
  }
  private isTargetInViewport(rect: any, viewportRect: any): boolean {
    if (!rect || !viewportRect) return false
    const { left, top, right, bottom, width, height } = rect
    const vh = viewportRect.height
    const vw = viewportRect.width
    // console.log(vh, vw, left, top, right, bottom, width, height)
    // 任意像素展现即认为展现
    // todo 判断条件不够严谨
    // 1. 不判断元素本身的宽高是否为零，因为存在父容器无宽高的情况 
    if (
      ((left >= 0 && left < vw) || (left < 0 && left + width > 0)) &&
      ((top >= 0 && top < vh) || (top < 0 && top + height > 0))
    ) {
      return true
    }
    return false
  }
}
