class StatsDebugger {
  private logsCache: any[] = []
  private isDebug: boolean = true
  private isInfo: boolean = true
  switch(isDebug: boolean) {
    this.isDebug = isDebug
  }
  group(label: any) {
    if (this.isDebug) {
      console.group('%c _ZS_ ' + label, 'background: #666; color: #badaff')
    }
  }
  groupEnd() {
    if (this.isDebug) {
      console.groupEnd()
    }
  }
  log(...args: any[]) {
    if (this.isDebug) {
      console.log('%c _ZS_LOG_ ', 'background: #666; color: #badaff', ...args)
    }
  }
  info(...args: any[]) {
    if (this.isInfo) {
      console.log('%c _ZS_INF_ ', 'background: #666; color: #bada55', ...args)
    }
  }
  sys(...args: any[]) {
    console.log('%c _ZS_SYS_ ', 'background: #666; color: #ffffff', ...args)
  }
  error(...args: any[]) {
    console.log('%c _ZS_ERR_ ', 'background: #666; color: #ff5555', ...args)
  }
  pushRecord(log: any) {
    this.logsCache.push(log)
  }
  clearRecords(log: any) {
    this.logsCache = []
  }
  groupRecords() {
    console.log('%c _ZS_RECORDS_GROUP_ ', 'background: #666; color: #bada55')
    console.group('已打点位')
    this.logsCache.forEach((v, i) => {
      console.log(i, v.name, v.lnc, v.pageUUID)
    })
    console.groupEnd()
  }
  tableRecords() {
    console.log('%c _ZS_RECORDS_TABLE_ ', 'background: #666; color: #bada55')
    console.table(this.logsCache, ['name', 'lnc', 'pageUUID'])
  }
}

export const D = new StatsDebugger()
